import React from 'react';
import styled from "styled-components";

const Field = styled.input`
  display: inline-block;
  height: 24px;
  margin : 16px 24px 16px 8px;
  width: 4rem;
`
const Label = styled.label`
  margin : 8px;
  display: inline-block;
`

const Button = styled.button`
  display: inline-block;
  border-radius: 3px;
  padding: 0.5rem 0;
  margin: 0.1rem 0rem;
  width: 2.5rem;
  background: transparent;
  color: white;
  border: 2px solid white;
  vertical-align:middle;
  :hover{
    background: white;
    color: black;
    cursor: pointer;
  }

`
const Range = styled.input`

`

const Grade = (props) => {
  return(
    <div>
    <p>{props.id} Grade: {props.grade}% | Weight: {props.weight}% <Button type="button" onClick={props.onClick}>Ã—</Button></p>
    <Range 
          name="grade"
          type="range"
          value={props.grade}
          min="0" 
          max="100"
          step="1"
          onChange={props.onChange}
        />
    </div>
    
  )
}



const GradeList = (props) => {
  const listItems = props.grades.map(({grade,weight},i) => 
    <Grade 
      grade={grade}
      weight={weight}
      onClick={() => props.onClick(i)}
      key={i}
      onChange={(e)=>props.onGradeChange(e,i)}
    />
  );
  return(
    <div>{listItems}</div>
  )
}

const GradeResult = (props) => {
  const sum_grade = props.grades.reduce((acc,grade)=>acc+grade.grade*grade.weight,0);
  const result =  sum_grade/props.total_weight;
  const weight_left = 100-props.total_weight;
  const target = (props.target - sum_grade/100)/weight_left *100

  let message;
  if(target > 100){
    message = <h4>You cannot achieve {props.target +'%'} :(</h4>
  }else if(target>0){
    message = <h4>To get {props.target + '%'}, you need {target.toPrecision(4) + '%'} overall with {weight_left.toPrecision(4)+'%'} weighting</h4>
  }else{
    message = <h4>You have already reached {props.target + '%'}!, Well done!</h4>
  }
  if (weight_left === 0){
    return(
    <h2>Overall average: {result.toPrecision(4)}</h2>
    )
  }else{
    return (
      <div>
        <h2>Current average: {result.toPrecision(4)}</h2>
        <h3>Min: {(sum_grade/100).toPrecision(4)} | Max: {(weight_left + sum_grade/100).toPrecision(4) }</h3>
        {message}
      </div>
    )
  }
}

const TargetSlider = (props)=>{
  return(
    <Label>
        Target
        <Range 
          name="target"
          type="range"
          placeholder="70"
          value={props.target}
          min="0" 
          max="100"
          step="1"
          onChange={props.onChange}
        />
        <Field 
          name="target"
          type="number"
          placeholder="70"
          value={props.target}
          min="0" 
          max="100"
          step="1"
          onChange={props.onChange}
        />
    </Label>
  )
}

const GradeInput = (props) =>{
  return (
    <form onSubmit={props.onSubmit}>
        <Label>
          Grade:
          <Field 
            name="grade" 
            type="number" 
            placeholder={70} 
            value={props.grade} 
            onChange={props.onChange} 
            min="0" 
            max="100"
            step="any"
          />
        </Label>
        <Label>
          Weight:
          <Field 
            name="weight" 
            type="number" 
            placeholder={30} 
            value={props.weight} 
            onChange={props.onChange} 
            min="0" 
            max={props.weight_left} 
            step="any"
          />
        </Label>
        <Button type="submit" value="Submit">+</Button>
      </form>
  )
}


class GradeForm extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      grade : '',
      weight: '',
      grades: props.grades,
      target: 60};
    this.onChange = this.onChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
    this.onClick = this.onClick.bind(this);
    this.onGradeChange = this.onGradeChange.bind(this);
  }

  onGradeChange(e,i){
    const value = e.target.value;
    const prop = e.target.name;
    const grades = this.state.grades.slice();
    grades[i][prop] = value;
    this.setState({
      grades:grades
    })
  }

  onChange(e) {
    const value = e.target.value;
    const name = e.target.name;
    this.setState({
      [name]:value
    });
  }

  onClick(i){
    const grades = this.state.grades
    grades.splice(i,1);
    this.setState({
      grades:grades
    })
  }

  onSubmit(e) {
    const grades = this.state.grades;
    if(this.state.grade !=='' && this.state.weight !==''){
      this.setState({
      grades : [{
        grade : this.state.grade,
        weight :  this.state.weight
      }].concat(grades),
      grade : '',
      weight: ''
      })
    }
    e.preventDefault();
  }

  render() {
    const total_weight = this.state.grades.reduce((acc,grade)=>acc+Number(grade.weight),0);
    const weight_left = 100-total_weight;
    if (weight_left === 0){
      return (
        <div>
          <GradeResult grades={this.state.grades} total_weight={total_weight} target={this.state.target}/>
          <GradeList grades={this.state.grades} onClick={this.onClick} onGradeChange={this.onGradeChange} weight_left={weight_left}/>
        </div>
      );
    }else{
      return (
        <div>
          <GradeResult grades={this.state.grades} total_weight={total_weight} target={this.state.target}/>
          <TargetSlider target={this.state.target} onChange = {this.onChange} />
          <GradeInput onChange={this.onChange} onSubmit={this.onSubmit} grade = {this.state.grade} weight={this.state.weight}  weight_left={weight_left}/>
          <GradeList grades={this.state.grades} onClick={this.onClick} onGradeChange={this.onGradeChange}/>
        </div>
      );
    }
    
  }
}

export default GradeForm;